services.service('UserControl', [
    'UserService', '$state', 'constants',
    function (UserService, $state, Const) {

        this.getState = function () {
            var user = UserService.get();
            if (!user) return;

            if (!user.tnc && Const['tncPopup']) {
                return 'tnc'
            }

            if (!user.activeTradingAccount) {
                switch (user.registrationState) {
                    case Const.userState.initial:
                        return 'signupPersonalProfile';
                    case Const.userState.personal:
                    case Const.userState.questionnaire:
                        return 'signupQuestionnaire';
                }
            }
        };

        return this;
    }
]);
