// Ionic Starter App

// angular.module is a global place for creating, registering and retrieving Angular modules
// 'starter' is the name of this angular module example (also set in a <body> attribute in index.html)
// the 2nd parameter is an array of 'requires'
// 'starter.controllers' is found in controllers.js


var app = angular.module('forex-mobile', ['ionic', 'ngAnimate', 'app.controllers', 'app.services', 'dynform', 'appsflyer', 'ionic-toast', 'dynform', 'ngCookies', '720kb.socialshare']);

var controllers = angular.module('app.controllers', ['ngMessages', 'ui.utils.masks', 'ngMask', 'angularMoment']);
var models = angular.module('app.models', ['angularMoment']);
var collections = angular.module('app.collections', []);
var services = angular.module('app.services', ['app.models', 'app.collections', 'ionic', 'angular-md5']);

app.run(['$ionicPlatform', '$ionicLoading', '$injector', 'config', 'constants', 'Storage', 'appsflyer', '$rootScope', '$state', '$q',
    '$urlRouter', 'Utils',
    function ($ionicPlatform, $ionicLoading, $injector, config, Const, Storage, appsflyer, $rootScope, $state, $q, $urlRouter, Utils) {

        $ionicPlatform.ready(function () {
            ionic.Platform.isFullScreen = true;

            log('App run phase.');
            Storage.set('platform', ionic.Platform.platform());
            $ionicLoading.show();

            $injector
                .get('Init')
                .run()
                .then(function () {
                    $ionicLoading.hide();
                    deregisterFunction();
                    Utils.getURLParameterByName('action') === Const.depositActionName ? $state.go('root.app.wallet') : $state.go('root.app.assets')
                })
                .catch(function (err) {
                    log('Init run error: ', err);
                    deregisterFunction();
                    $ionicLoading.hide();
                    if (!(document.location.hash.match(/login/) || document.location.hash.match(/signup/))) {
                        return $state.go('welcome')
                    }
                    $urlRouter.sync();
                })

        });

        $rootScope.$on('$stateChangeError', function (event, toState, toParams, fromState, fromParams, error) {
            log('$stateChangeError: ', error, fromState, toState);
            event.preventDefault();
            $ionicLoading.hide();
            $state.go('welcome');
        });

        var deregisterFunction = $rootScope.$on('$stateChangeStart', function (event) {
            event.preventDefault();
        });

        $rootScope.$on('$stateChangeStart', function (ev, to, toParams, from, fromParams) {
            var constants = $injector.get('constants')
            if (constants['disabledStates'] && constants['disabledStates'].indexOf(to.name) > -1) {
                console.warn('State disabled: [', to.name, ']')
                $state.go('welcome');
                ev.preventDefault()
            }
        })
    }]);


