    function depositResolver(Storage, constants, $ionicLoading, rootResolver) {
        $ionicLoading.show({noBackdrop: true});
        return Storage
            .load('depositValue')
            .then($ionicLoading.hide)
            .catch(function (error) {
                Storage.set('depositValue', constants.defaultDeposit);
                $ionicLoading.hide();
            });
    }

    function demoDepositResolver(Storage, constants, $ionicLoading, $q, rootResolver) {
        var demoDepositValues = Storage.get('demoDepositValues');
        if (!!demoDepositValues && demoDepositValues.length) {
            return $q.resolve()
        }
        $ionicLoading.show({noBackdrop: true});

        return Storage
            .load('demoDepositValues')
            .then($ionicLoading.hide)
            .catch(function (error) {
                Storage.set('demoDepositValues', []);
                $ionicLoading.hide();
            });
    }

    function depositPaymentMethods(Storage, constants, $ionicLoading, rootResolver) {
        $ionicLoading.show({noBackdrop: true});
        return Storage
            .load('paymentMethods')
            .then($ionicLoading.hide)
            .catch(function (error) {
                $ionicLoading.hide();
            });
    }

    function sendLocation(location) {
        return function (LocationService) {
            LocationService.enter(location);
            return true;
        }
    }

