app.directive('proChart', ['$timeout', '$ionicLoading', 'config', 'ServerTimeService', 'ProChartService', 'LocaleService',
    function ($timeout, $ionicLoading, config, ServerTimeService, ProChartService, LocaleService) {
        return {
            scope: {
                asset: '<',
                chart: '<'
            },
            replace: true,
            link: function (scope, element) {
                var objChart,
                    loadTimeout,
                    chartContext = {
                        objChartContext: null,
                        objRequestContext: null,
                        strRequest: null
                    },
                    timeZone = -1* ServerTimeService.timeZoneOffset * 60,
                    amountBarsShow = 35,
                    historyType = {
                        timestamp: 0,
                        open: 1,
                        high: 2,
                        low: 3,
                        close: 4
                    },
                    chartType = {
                        'line': 'line',
                        'areaspline': 'area',
                        'ohlc': 'line',
                        'candlestick': 'candlesticks'
                    },
                    chartRange = {
                        '1min': '1m',
                        '5min': '5m',
                        '15min': '15m',
                        '30min': '30m',
                        '1hour': '60m',
                        '4hours': '4h',
                        '1day': '1d',
                        '1week': '1w',
                        '1month': '1n'
                    },
                    assetUpdateWatch;

                loadTimeout && $timeout.cancel(loadTimeout);

                var assetSelect = scope.$watch('chart.ready', function (newVal, oldVal) {
                    if (newVal !== oldVal && newVal) {
                        scope.loadingMessage = LocaleService.translation.loading_chart;
                        $ionicLoading.show({templateUrl: 'templates/directives/message-spinner.html', scope: scope});
                        return objChart ? updateChart() : createChart();
                    }
                });

                function watchPoint() {
                    return scope.$watch('asset.bid', function (newVal, oldValue) {
                        if (newVal && newVal !== oldValue && scope.chart.ready) {
                            updatePoint();
                        }
                    });
                }

                var chartTypeWatch = scope.$watch('chart.type', function (newVal, oldVal) {
                    if (newVal && newVal !== oldVal && scope.chart.ready) {
                        objChart && chartRedrawType(chartType[newVal.type]);
                    }
                });


                function createChart() {
                    objChart = null;
                    if (!ProChartService.loaded) {
                        var prochartloader = ProChartService.getLoader();
                        prochartloader.LoaderInitComplete = eventLoaderComplete;
                        prochartloader.LoaderInitCombine();
                        loadTimeout = $timeout(function () {
                            if (scope.chart) {
                                scope.chart.loadstate = 'fail';
                            }
                            $ionicLoading.hide();
                            $timeout.cancel(loadTimeout);
                        }, 20000);

                    } else {
                        eventLoaderComplete();
                    }


                    function eventLoaderComplete() {
                        var chartTheme = config['chartTheme'] || 'default';
                        objChart = ProChart_InitLayout("#chart", "layout-mobile", chartTheme);
                        objChart.apiinit.rootpath = config.chartUrl;

                        objChart.events.layout.readyComplete = eventlayoutReadyComplete;
                        objChart.gui.loadLayout();
                        objChart.events.layout.initComplete = eventlayoutInitComplete;
                        objChart.events.chart.createComplete = function () {
                            objChart.chart.GetNumOfCandles && objChart.chart.SetZoom(objChart.chart.GetNumOfCandles() - amountBarsShow, objChart.chart.GetNumOfCandles(), true);
                        }
                    }

                    function eventlayoutReadyComplete() {
                        objChart.api.layout.set('width||height', element[0].clientWidth + "||" + element[0].clientHeight);
                        if (scope.chart) {
                            scope.chart.loadstate = 'success';
                        }

                        loadTimeout && $timeout.cancel(loadTimeout);
                    }
                }

                function eventlayoutInitComplete() {
                    objChart.chart.DateFormat = {
                        "realtime": "DD/MM/YYYY HH:mm:ss",
                        "minutes": "HH:mm",
                        "hours": "DD/MM/YYYY HH:mm:ss",
                        "eod": "DD/MM/YYYY",
                        "datebarYear": "YYYY",
                        "datebarMonthDay": "DD/MM",
                        "datebarDay": "DD",
                        "datebarFull": "DD/MM/YYYY",
                        "datebarHoursMinutes": "HH:mm",
                        "datebarMonth": "MM"
                    };

                    objChart.chart.WaterMark = false;
                    objChart.chart.StudiesToLoad = [];
                    objChart.chart.ShowBarData = "false";
                    objChart.chart.hAxis.hPadInPixelsLeft = 10;
                    objChart.chart.vAxis.vPadInPixelsTop = 50;
                    objChart.chart.PriceChartType = chartType[scope.chart.type.type];
                    objChart.chart.Symbol = scope.asset.outputName;
                    objChart.chart.SymbolName = scope.asset.outputName;
                    objChart.chart.TimeScale = chartRange[scope.chart.range.title];
                    objChart.chart.Precision = scope.asset.digits;
                    objChart.chart.TimeZone = timeZone;


                    objChart.chart.JSPush = {
                        enableHistory: true,
                        enableRT: true,
                        enableSymbols: true,
                        enableStudies: true,
                        funcFetchData: getChartData
                    };

                    ProChartService.loaded = true;
                    loadTimeout && $timeout.cancel(loadTimeout);
                }

                function getChartData(objChartContext, objRequestContext, strRequest, objParameters) {
                    switch (strRequest.toLowerCase()) {
                    case "getrecenthistory":
                        var data = convertHistory(scope.chart.data);
                        objChart.chart.Push(objChartContext, objRequestContext, strRequest, data);
                        $ionicLoading.hide();

                        //unsubscribe
                        assetUpdateWatch && assetUpdateWatch();
                        //subscribe
                        assetUpdateWatch = watchPoint();

                        return;
                    case "getmultirt":
                        chartContext = {
                            objChartContext: objChartContext,
                            objRequestContext: objRequestContext,
                            strRequest: strRequest
                        };
                    }
                }

                function convertHistory(data) {
                    data[data.length - 1][historyType.close] = scope.asset.markupBid();
                    return {
                        id: scope.asset.outputName,
                        lastTimeStamp: data[data.length - 1][historyType.timestamp],
                        data: data.map(function (candle) {
                            return {
                                open: candle[historyType.open],
                                close: candle[historyType.close],
                                low: candle[historyType.low],
                                high: candle[historyType.high],
                                other: 0,
                                date: candle[historyType.timestamp]
                            }
                        })
                    }
                }

                function updatePoint() {
                    var time = ServerTimeService.time,
                        data = [{
                            id: scope.asset.outputName,
                            lastTimeStamp: time,
                            data: [{
                                bid: scope.asset.markupBid(),
                                ask: scope.asset.markupAsk(),
                                other: 0,
                                date: time
                            }]
                        }];

                    objChart &&
                    objChart.chart &&
                    objChart.chart.Push(
                        chartContext.objChartContext,
                        chartContext.objRequestContext,
                        chartContext.strRequest,
                        data
                    );
                }

                function chartRedrawType(type) {
                    type && objChart.chart.ChangePriceType(type);
                }

                function chartRedrawRange(range) {
                    objChart && objChart.chart.ChangeTimeScale(range);
                    objChart && objChart.chart.SetZoom(objChart.chart.GetNumOfCandles() - amountBarsShow, objChart.chart.GetNumOfCandles(), true);
                }

                function updateChart() {
                    objChart && objChart.chart.ChangeSymbol({
                        id: scope.asset.outputName,
                        name: scope.asset.description,
                        precision: scope.asset.digits
                    });
                    chartRedrawRange(chartRange[scope.chart.range.title]);
                }

                element.on('$destroy', function () {
                    loadTimeout && $timeout.cancel(loadTimeout);
                    objChart && objChart.chart.StopRTRequests();
                    assetUpdateWatch && assetUpdateWatch();
                    assetSelect && assetSelect();
                    chartTypeWatch && chartTypeWatch();
                    try {
                        objChart && objChart.chart.CleanUp();
                        document.getElementById('chart').innerHTML = '';
                    } catch (e) {
                        console.warn('chart cleanup failed', e);
                    }

                    objChart = null;
                    scope.asset = null;
                    scope.chart = null;
                })
            },
            template: '<div style="position: relative" class="chart" id="chart"></div>'
        }
    }]);
