services.service('ProfileRulesHelper', [
    function () {

        function ProfileRulesHelpder(_rules, scope) {
            var self = this,
                rules = filterByScope(_rules, scope);

            this.get = function () {
                return rules;
            };

            function filterByScope(_rules, scope) {
                return Object.keys(_rules)
                    .reduce(function (acc, field) {
                        //constructing rule object with properties of then current scope
                        acc[field] = Object.assign({}, _rules[field], _rules[field].scopes[scope]);
                        return acc;
                    }, {})
            }

            this.isHidden = function (field) {
                return rules[field].hidden;
            };

            this.isVisible = function (field) {
                return !rules[field].hidden;
            };

            this.isReadonly = function (field, value) {
                if (!value) {
                    return rules[field].readonly;
                }

                return rules[field].readonly || (value && rules[field].readonlyIfNotEmpty);
            };

            this.isRequired = function (field) {
                return rules[field].required;
            };

            this.getRawName = function (field) {
                return rules[field].rawName;
            };

            /**
             * Returns profile rule by field raw name
             * @param rawName
             */
            this.getRuleByRawName = function (rawName) {
                const field = Object.keys(rules)
                    .find(function (field) {
                        return rules[field].rawName === rawName
                    });
                return field && rules[field]
            }
        }

        return ProfileRulesHelpder;

    }
]);
